<template>
  <div>
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-statistics' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item>业务数据统计</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <div class="query-time-container statistics">
          <el-select v-model="valueTime" placeholder="Select" v-show="isShowSelectTime" @change="onChangeSelectTime">
            <el-option v-for="item in optionTime" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <div v-show="!isShowSelectTime">
            <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="-" start-placeholder="开始时间"
              end-placeholder="结束时间" @change="changeTimeline" />
          </div>
          <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
        </div>
        <div class="statistics-container">
          <el-row>
            <el-col :span="5">
              <div class="left-content">
                <div class="content">
                  <p>用户</p>
                  <img src="../assets/images/kh.png" alt="user">
                </div>
              </div>
            </el-col>
            <el-col :span="14">
              <div class="middle-content">
                <div class="progress-left">
                  <div class="progress-item">
                    <div class="line-box">
                      <div class="line line-1"></div>
                      <i class="el-icon-arrow-right arrow-1"></i>
                    </div>
                  </div>
                  <div class="progress-text">
                    <div class="text-box">
                      <div class="item">
                        <div class="title">请求次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求成功次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求失败次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求平均耗时</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求耗时中位数</div>
                        <div class="detail">0</div>
                      </div>
                    </div>

                  </div>
                </div>
                <div class="content">
                  <p>WAF</p>
                  <img src="../assets/images/logo.png" alt="logo">
                </div>
                <div class="progress-right">
                  <div class="progress-item">
                    <div class="line-box">
                      <div class="line line-2"></div>
                      <i class="el-icon-arrow-right arrow-2"></i>
                    </div>
                  </div>
                  <div class="progress-text">
                    <div class="text-box">
                      <div class="item">
                        <div class="title">请求次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求成功次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求失败次数</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求平均耗时</div>
                        <div class="detail">0</div>
                      </div>
                      <div class="item">
                        <div class="title">请求耗时中位数</div>
                        <div class="detail">0</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </el-col>
            <el-col :span="5">
              <div class="right-content">
                <div class="content">
                  <p>业务服务器</p>
                  <img src="../assets/images/yw.png" alt="server">
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
        <div style="padding: 15px 0 5px 0;border-top: 1px solid #ebeef5;color: #4e5969;">
          <p>业务服务器负载情况</p>
        </div>
        <div class="demo-block">
          <el-table :data="tableDataDetail" style="width: 100%">
            <el-table-column prop="node_uuid" label="域名"></el-table-column>
            <el-table-column prop="node_hostname" label="回源地址"></el-table-column>
            <el-table-column prop="node_ip" label="回源次数"></el-table-column>
            <el-table-column prop="node_ip" label="回源成功次数"></el-table-column>
            <el-table-column prop="node_ip" label="回源失败次数"></el-table-column>
            <el-table-column prop="node_ip" label="回源平均耗时"></el-table-column>
            <el-table-column prop="node_ip" label="回源耗时中位数"></el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>

</template>
<script>
import { mixin, JXAjax, formatterDateTime } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      dataSearch: "",
      loadingPage: false,
      loading: false,
      tableData: {},
      tableDataDetail:[],
      isShowSelectTime: true,
      valueTime: "1w",
      pickerTime: [],
      optionTime: [
        { value: '1h', label: '1小时' },
        { value: '1d', label: '24小时' },
        { value: '1w', label: '7天' },
        { value: '1m', label: '30天' },
        { value: 'default', label: '自定义' }
      ],
    };
  },
  computed: {
    rules() {
      return {
      }
    }
  },
  
  mounted() {
    this.onChangeSelectTime()
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "post",
        "/soc/soc_query_request_statistics",
        {
          from_time: formatterDateTime(t.pickerTime[0]),
          to_time: formatterDateTime(t.pickerTime[1]),
        },
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data;
          console.log(t.tableData)
        },
        function () {
          t.loadingPage = false;
        }
      );
    },
    getDataDetail() {
      var t = this;
      JXAjax(
        "get",
        "/soc/soc_query_request_statistics_detail",
        {},
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
        },
        function () {
          t.loadingPage = false;
        }
      );
    },
    onChangeSelectTime() {
      var t = this;
      if (t.valueTime == 'default') {
        t.isShowSelectTime = false;
        t.pickerTime = [
          new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
          new Date()
        ];
      } else {
        t.isShowSelectTime = true;
        if (t.valueTime == '1h') {
          t.pickerTime = [
            new Date(new Date().getTime() - 1 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1d') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1w') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 7),
            new Date()
          ];
        }
        if (t.valueTime == '1m') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30),
            new Date()
          ];
        }
      }
    },
    changeTimeline(event) {
      var t = this;
      if (event == null) {
        t.isShowSelectTime = true;
        t.valueTime = '1w'
      } else {
        t.isShowSelectTime = false;
      }
    },
    onChangeSearch() {
      this.onChangeSelectTime()
      this.getData()
    },

  },
};
</script>
<style>
.statistics-container .item{
  display: flex;
    justify-content: center;
    margin:10px;
    color: #4e5969;
}
.statistics-container .title{
  display: flex;
  height: auto;
  line-height: 1.5;
  width: 120px;
  justify-content: left;
  color: #4e5969;
}
.statistics-container .detail {
  display: flex;
  height: auto;
    line-height: 1.5;
}

.statistics-container .middle-box {
  width: 100%;
  position: relative;
}
.statistics-container .progress-item {
  display: flex;
}
.statistics-container .el-icon-arrow-right {
  font-size: 52px;
  font-weight: bold;
  color: #409eff;
  display: block;
  position: relative;
  opacity: 0;
  animation: opacity 3s ease-in-out infinite;
}
.statistics-container .line-box {
  position: relative;
  line-height: 1;
  display: flex;
  align-items: center;
  padding: 27px 0px;
  width: 100%;
}
.statistics-container .line {
  display: block;
  width: 100%;
  height: 6px;
  background: #409eff;
  border-radius: 5px;
  animation: progress 3s ease-in-out infinite;
  opacity: 0;
}
.statistics-container .el-icon-arrow-right:before {
  position: absolute;
  top: -26px;
  left: -32px;
}

@keyframes progress {
  0% {
    opacity: 0;
    width: 0%;
  }
  100% {
    opacity: 1;
    max-width: 100%;
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.left-content, .right-content, .middle-content{
  display: flex;
    align-items: center;
    justify-content: center;
}

 .statistics-container .content {
  margin-top: 20px;
}
.statistics-container .content img {
  width: 100%;
  max-width: 200px;
  display: block;
}
.statistics-container .content p {
  text-align: center;
    font-size: 26px;
    font-weight: 600;
    line-height: 1.5;
}
.statistics-container .left-content img {
  padding-top: 10px;
}
.statistics-container .middle-content img {
  padding-top: 30px;
  max-width: 140px;
}
.statistics-container .right-content img {
  max-width: 260px;
}
.statistics-container .middle-content {
  position: relative;
}
.statistics-container .progress-left {
  position: absolute;
  top: 10px;
  left: 2%;
  width: 33%;
}
.statistics-container .progress-right{
  position:absolute;
  top:10px;
  left: 66%;
  width: 33%;
}
.statistics-container .progress-right .arrow-2{
  animation-delay: 1.5s
}
.statistics-container .progress-right .line-2 {
  animation-delay: 1.5s
}
.statistics-container .progress-text {
  width: 100%;
  display: flex;
  justify-content: center;
}
.statistics-container .text-box {
  width: 100%;
  border-radius: 5px;
  background-color: #f9f9f9;
  padding: 10px;
  box-sizing: border-box;
  max-width: 200px;
  margin-top: 15px;
}
.query-time-container.statistics .el-button {
  margin-left: 10px;
}
.query-time-container.statistics {
  display: flex;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(235, 238, 245);
}
</style>
